@use 'global' as *;

.pc-nav {

}

.pc-nav__items {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
}

.pc-nav__item,
.pc-nav__item a {
  height: inherit;
}

.pc-nav__item a {
  display: flex;
  align-items: center;
  color: $black;
  font-size: rem(16);
  font-family: $english-family;
  letter-spacing: -0.004em;
  font-weight: $medium;
  line-height: 1;
  padding-left: rem(40);
  //padding-right: rem(12);
}

.pc-nav__item:first-child a {
  padding-left: 0;
}

