@charset "UTF-8";
/* リキッドレイアウト対応 */
body {
  font-family: "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.6666666667vw;
  }
}
@media (min-width: 960px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.footer {
  padding-top: 3.75rem;
  padding-bottom: 3.5625rem;
  background-color: #3E3E3E;
}

@media screen and (min-width: 768px) {
  .footer__wrapper {
    display: flex;
  }
}

.footer__contents {
  text-align: center;
}

.footer__icon {
  display: flex;
}

.footer__x {
  background-color: #FFFFFF;
  opacity: 0.6;
}

.footer__items {
  color: #FFFFFF;
  margin-top: 2.625rem;
}

.footer__item {
  display: block;
}

.footer__item a {
  font-size: 0.875rem;
  font-weight: 500;
  letter-spacing: 0.0035em;
}

.footer__item:not(:first-child) {
  padding-top: 1.625rem;
}

.footer__title {
  text-align: center;
  margin-top: 2.5rem;
}

.footer__logo {
  color: #E3E3E3;
  font-size: 2rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  line-height: 1;
}

.footer__copyright {
  margin-top: 0.75rem;
  color: #FFFFFF;
  opacity: 0.6;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.003em;
}

.footer__copyright small {
  font-size: 0.75rem;
}

.hamburger {
  position: fixed;
  width: 1.625rem;
  height: 1.25rem;
  top: 1.5rem;
  right: 1rem;
  z-index: 1000;
  display: block;
  transition: transform 0.5s ease 0s;
}
@media screen and (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.hamburger span {
  position: relative;
  width: inherit;
  height: 0.125rem;
  background-color: #3E3E3E;
  transition: transform 0.5s ease 0s;
}

.hamburger span:nth-child(1) {
  position: absolute;
  top: 15%;
}

.hamburger span:nth-child(2) {
  position: absolute;
  top: 51%;
  opacity: 1;
}

.hamburger span:nth-child(3) {
  position: absolute;
  top: 85%;
}

.hamburger.is-active {
  transform: translateX(-19.75rem);
}

.hamburger.is-active span:nth-child(1) {
  top: 60%;
  transform: rotate(45deg);
  background-color: #FFFFFF;
}

.hamburger.is-active span:nth-child(2) {
  top: 51%;
  opacity: 0;
}

.hamburger.is-active span:nth-child(3) {
  top: 60%;
  transform: rotate(-45deg);
  background-color: #FFFFFF;
}

.header {
  height: 4.25rem;
  background-color: #FFFFFF;
  z-index: 1000;
  position: fixed;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .header {
    background-color: rgba(255, 255, 255, 0.1019607843);
    height: 5.1875rem;
  }
}

.header__inner {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.9375rem;
}
@media screen and (min-width: 768px) {
  .header__inner {
    justify-content: center;
  }
}

.header__logo {
  font-size: 1.4375rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  color: #3E3E3E;
  line-height: 1;
}
@media screen and (min-width: 768px) {
  .header__logo {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .header__drawer {
    display: none;
  }
}

.header__pc-nav {
  height: inherit;
  display: none;
}
@media screen and (min-width: 768px) {
  .header__pc-nav {
    display: block;
  }
}

.pc-nav__items {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
}

.pc-nav__item,
.pc-nav__item a {
  height: inherit;
}

.pc-nav__item a {
  display: flex;
  align-items: center;
  color: #3E3E3E;
  font-size: 1rem;
  font-family: "Montserrat", sans-serif;
  letter-spacing: -0.004em;
  font-weight: 500;
  line-height: 1;
  padding-left: 2.5rem;
}

.pc-nav__item:first-child a {
  padding-left: 0;
}

.section-header {
  text-align: center;
  font-size: 1.875rem;
  font-weight: 600;
  color: #3E3E3E;
  font-family: "Montserrat", sans-serif;
  line-height: 1;
}
@media screen and (min-width: 768px) {
  .section-header {
    font-size: 2.25rem;
  }
}

.sp-nav {
  position: absolute;
  top: 0;
  right: 0;
  width: 18.75rem;
  max-width: 100%;
  transform: translateX(105%);
  transition: transform 0.5s ease 0s;
  height: 100vh;
  background-color: #FFFFFF;
  z-index: 999;
}

.sp-nav.is-active {
  transform: translateX(0);
}

.sp-nav__items {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10rem 0;
  min-height: 100vh;
}

.sp-nav__item {
  width: 100%;
}

.sp-nav__item a {
  color: #3E3E3E;
  text-align: center;
  display: block;
  padding: 1rem 0;
  font-family: "Montserrat", sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1;
}

.sp-nav__background {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #3E3E3E;
  opacity: 0.7;
  z-index: 998;
}

.sp-nav__background.is-active {
  display: block;
}

.space {
  margin-top: 1.25rem;
}